.App{
    text-align: center;
}

.box{
    max-width: 300px;
    margin: 50px auto;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding: 10px;
}

p{
    font-size: 50px;
    text-align: center;
}

button{
    padding: 10px;
    margin: 10px;
    font-size: 18px;
    color: #ffffff;
    border: none;
    border-radius: 10px;
}

button.add{
    background-color: #18978f;
}

button.sub{
    background-color: #eb4747;
}

button.reset{
    background-color: darkblue;
}